* {
  margin: 0;
}
body {
  background-color: #f5f6fa;
}
.App {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
.app__dropdown {
  background-color: white;
}
.app__header > h1 {
  color: black;
  font-size: 2rem;
  font-weight: 700;
  font-stretch: expanded;
  text-decoration : underline;
}
.app__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}
.app__left{
  flex: 0.8;
}
.app__right {
  display: flex;
  flex-direction: space-between;
}
.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.app__graph {
  flex-grow: 1;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 990px){
  .App{
    flex-direction: column;
  }
}
.app__graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}