.map {
  height: 500px;
  background-color: white;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 15px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.7);
}
.map .leaflet-container {
    height: 100%;
}
.info-flag {
    height: 40px;
    width: 60px;
    background-size: cover;
    border-radius: 8px;
}
.info-flag img {
    width: 100px;
    border-radius: 5px;
}
.info-container {
    width: 150px;
}
.info-cases, info-recovered, info-deaths {
    font-size: 16px;
    margin-top: 5px;
}
.info-name {
    font-size: 20px;
    font-weight: bold;
    color: #555;
}