.infoBox {
    flex: 1;
    cursor: pointer;
}
.infoBox:not(:last-child) {
    margin-right: 10px;
}
.infoBox__cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.infoBox__total {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}
.infoBox--selected {
    border-top: 7px solid rgba(161, 245, 35, 0.815);
}
.infoBox--Red {
    border-color: red;
}
.infoBox__cases--green {
    color: rgba(161, 245, 35, 0.815) !important;
}